.footer {
    color: #fff;
}
.footer__inner {
    max-width: 1240px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 120px;
}
.footer__copy {
    font-size: 1.4rem;
}
.social {
    display: flex;
    border-bottom: 5px solid rgba(255,255,255, 0.2);
    padding: 0 0 10px 0;
}
.social li {
    padding: 0 8px;
}
.social li a {
    display: block;
    width: 16px;
    height: 16px;
}
.social__instagram {
    background: url(../Footer/img/instagram-logo.png) no-repeat;
}
.social__linkedin {
    background: url(../Footer/img/linkedin-logo.png) no-repeat;
}
.social__behance {
    background: url(../Footer/img/behance.png) no-repeat;
}


@media only screen and (max-width: 767px) {
    
    .footer {
        padding: 15px 0;
    }
    
    .footer__inner {
        flex-wrap: wrap;
        padding: 5px;
    }
    .footer__copy {
        flex-basis: 100%;
    }

}

